namespace $ { export class $saprun_logo extends $mol_view {

	/// sub / 
	/// 	<= Image - 
	/// 	<= Title -
	sub() {
		return [].concat( this.Image() , this.Title() )
	}

	/// Image $mol_image uri \-/saprun/logo/logo.svg
	@ $mol_mem()
	Image() {
		return (( obj )=>{
			obj.uri = () => "-/saprun/logo/logo.svg"
			return obj
		})( new this.$.$mol_image )
	}

	/// Title $mol_view sub / <= title -
	@ $mol_mem()
	Title() {
		return (( obj )=>{
			obj.sub = () => [].concat( this.title() )
			return obj
		})( new this.$.$mol_view )
	}

} }

