namespace $.$$ {
	
	export class $saprun_prognoz_report extends $.$saprun_prognoz_report {
		
		edit() {
			return $mol_state_arg.value( 'edit' ) != null
		}

		type() {
			return $mol_state_arg.value( 'type' ) || 'table'
		}

		pages() {
			return [
				this.View() ,
				... this.edit() ? [ this.Edit() ] : []
			]
		}

		content() {
			switch( this.type() ) {
				case 'table' : return [ this.Table() ]
				case 'charts' : return [ this.Charts() ]
				default : throw new Error( `Unsupported type: ${ this.type() }` )
			}
		}

		@ $mol_mem_key()
		table( name : string ) {
			const json = ( this.$.$mol_http.resource( `-/saprun/prognoz/data/${ name }.json` ) as any ).json()
			return json.features.map( ( item : any )=> item.properties )
		}

		@ $mol_mem_key()
		dictionary( id : { table : string , key : string } ) {
			const next = {} as any 
			for( let item of this.table( id.table ) ) {
				next[ item[ id.key ] ] = item
			}
			return next
		}

		@ $mol_mem()
		facts() {
			const filter = {} as { [ field : string ] : string }

			for( let axis in this.axises() ) {
				const val = this.filter( axis )
				if( !val ) continue

				filter[ 'DIM_' + axis ] = val
			}
			
			const facts = this.table( 'FACTS' )
			.filter( ( item : any )=> {
				for( let field in filter ) {
					if( item[ field ] != filter[ field ] ) return false
				}
				return true
			} )

			return facts
		}

		@ $mol_mem_key()
		fact_dict( axis : string ) {
			const next = {} as any 
			for( let item of this.facts() ) {
				next[ item[ 'DIM_' + axis ] ] = item
			}
			return next
		}

		@ $mol_mem()
		data() {
			const facts = this.facts()

			const data = {} as { [ row : string ] : { [ col : string ] : number } }

			const axis_row = this.axis_row()
			const axis_col = this.axis_col()

			const row_dict = this.dictionary({ table : this.axis_row() , key : '.COORD' })

			for( let item of facts ) {
				const row = item[ 'DIM_' + axis_row ]
				const col = item[ 'DIM_' + axis_col ]
				if( !data[ row ] ) data[ row ] = { '' : row_dict[ row ] && row_dict[ row ][ 'NAME' ] }
				data[ row ][ col ] = item[ 'VAL_0' ]
			}

			return data
		}

		row_ids() {
			return Object.keys( this.fact_dict( this.axis_row() ) )
		}

		col_ids() {
			return [ '' , ... Object.keys( this.fact_dict( this.axis_col() ) ) ]
		}

		col_head_content( id : any ) {
			if( !id ) return []
			const record = this.dictionary({ table : this.axis_col() , key : '.COORD' })[ id ]
			return $mol_maybe( record && record[ 'NAME' ] )
		}

		filters() {
			return Object.keys( this.axises() ).map( axis => this.Filter( axis ) )
		}

		filter_title( axis : string ) {
			return this.axises()[ axis ]
		}

		@ $mol_mem_key()
		filter_dictionary( axis : string ) {
			const next = { '' : '' } as { [ id : string ] : string }

			const ids = Object.keys( this.dictionary({ table : 'FACTS' , key : 'DIM_' + axis }) )
			const dict =  this.dictionary({ table : axis , key : '.COORD' })

			for( let id of ids ) next[ id ] = dict[ id ] && dict[ id ][ 'NAME' ]
			
			return next
		}

		charts() {
			return Object.keys( this.axises() )
			.filter( axis => Object.keys( this.filter_dictionary( axis ) ).length > 2 )
			.map( id => this.Chart( id ) )
		}

		graph_title( id : string ) {
			return this.axises()[ id ]
		}

		/*graph_title( id : string ) {
			const dict = this.dictionary({ table : this.axis_col() , key : '.COORD' })
			return dict[ id ] ? dict[ id ][ 'NAME' ] : id
		}*/

		@ $mol_mem_key()
		graph_series( id : string ) {
			const dict = {} as { [ val : string ] : number }
			
			for( let item of this.facts() ) {
				const key = item[ 'DIM_' + id ]
				dict[ key ] = ( dict[ key ] || 0 ) + item[ 'VAL_0' ]
			}

			return dict
		}

	}
	
}
