namespace $.$$ {
	
	export class $saprun_prognoz extends $.$saprun_prognoz {
		
		report() {
			return $mol_state_arg.value( 'report' )
		}

		entered( next? : boolean ) {
			return $mol_state_session.value( 'entered' , next ) || false
		}

		pages() {
			if( !this.entered() ) return [ this.Enter() ]
			if( !this.report() ) return [ this.Menu() ]
			
			return this.report_pages()
		}

		event_enter() {
			this.entered( true )
		}

		report_title() {
			return this.reports()[ this.report() ].title + ' ( )'
		}

		links() {
			return Object.keys( this.reports() ).map( id => this.Link( id ) )
		}

		link_title( id : string ) {
			return this.reports()[ id ].title
		}
		
		link_arg( id : string ) {
			return {
				report : id ,
				type : this.reports()[ id ].type ,
			}
		}
		
	}
	
}
